<template>
  <div>
    <div class="max-box">
      <img class="book" src="../../assets/img/微信图片_20190608210203.jpg" alt>
      <div class="imp_box">
        <h2>账号登陆</h2>
        <input type="text" min="5" max="10" v-model="name" placeholder="请输入用户名">
        <br>
        <input type="password" min="6" max="15" v-model="desc" placeholder="请输入密码">
        <br>
        <cube-checkbox v-model="checked">
          我已阅读并接受<span style="color:#14B9C8">《服务协议》</span>
        </cube-checkbox>
        <!-- <input type="text" v-model="price" placeholder="书的价格"><br> -->
        <button @click="fn">进入书城</button>
      </div>
    </div>
    <cube-popup type="my-popup" :mask="false" ref="myPopup2">
      用户名或密码错误
    </cube-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "admin",
      desc: "123456",
      checked:true
    };
  },
  methods: {
    showPopup(refId) {
      const component = this.$refs[refId]
      component.show()
      setTimeout(() => {
        component.hide()
      }, 1000)
    },
    fn() {
      if (this.name == "admin" && this.desc == "123456" && this.checked) {
        this.$router.push("/home");
      } else {
        this.showPopup('myPopup2')
      }
      let obj = {
        name:this.name,
        desc:this.desc,
      }
      this.$store.commit("myaccount",obj);
    }
  }
};
</script>

<style lang="less" scoped>
.max-box {
  margin-top: 30%;
  .book {
    width: 2rem;
    height: 2rem;
    position: relative;
    left: 38%;
  }
}
.imp_box {
  margin: 0.5rem 0 1.2rem;
  text-align: center;
  
  h2 {
    color: #14B9C8;
    font-size: 0.5rem;
    margin: 0 auto;
  }
  input {
    border: 0.01rem solid #ccc;
    border-radius: 0.05rem;
    height: 0.6rem;
    width: 60%;
    border-top: 0;
    border-left: 0;
    border-right: 0;
     font-size: 0.4rem;
  }
  button {
    width: 4rem;
    height: 1rem;
    font-size: 0.4rem;
    color: aliceblue;
    border: 0;
    margin-top: 2rem;
    background-image: linear-gradient(to right, #14B9C8 0%, #1588F5 100%);
    border-radius: 0.3rem;
  }
}

</style>